@import url('https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap');
@import url('https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap');

:root{
  --dark1:#1D3557;
  --dark2:#457B9D;
  --average:#E63946;
  --light2:#A8DADC;
  --light1:#F1FAEE;
  --fontZen:'Zen Dots', cursive;
  --fontNewTagomin:'New Tegomin', serif;
}



body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:var(--dark1);
  font-family: var(--fontZen);
  overflow: hidden;
}

.menu{
  position: absolute;
  width: 100vw;
  display: block;
  z-index: 1;
  height: 80px;
}

.logo{
  padding: 16px;
  width: 30vw;
  float: left;
}

.logo a{
  color:var(--light1);
  font-size: 18px;
  text-decoration: none;
  letter-spacing: 3px;
  font-weight: bold;
}

.logo a:hover{
  color: var(--light2);
}


.logo img{
  position: relative;
  width:30px;
  height: 30px;
  top: 10px;
}



.menuLinks{
  padding: 30px;
  font-size: 12px;
  float: right;
}

.menuLinks a{
  text-decoration: none;
  padding: 12px;
  color: var(--light1);
}

.menuLinks a:hover{
  color: var(--light2);
  opacity: 0.5;
  text-shadow: 2px 2px 10px var(--light1),-2px -2px 10px var(--light1);
  border-radius: 10% 90% 30% 70%;
  background-color: var(--average);
}

.socials{
  position: absolute;
  display: flex;
  width: 100vw;
  justify-content: center;
  top:90%;
  transform: transform(-90%);
}

.socials a{
  margin-left: 10px;
  margin-right: 10px;
  text-decoration: none;
}

.socials a img{
  width:30px;
  height: 30px;
}


.main{
  display: grid;
  position: absolute;
  grid-template-columns: 40% 60%;
  width: 80vw;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  letter-spacing: 3px;
  font-size: 1.2rem;
  font-family: var(--fontNewTagomin);
  text-align: center;
  color: var(--light1);
}

.main mark{
  background: none;
  color: var(--average);
}

.main .mainContent{
  padding: 5%;
  font-size: 0.8em;
}

.main .mainContent .name{
  color: black;
  font-size: 2rem;
  padding-bottom: 20px;
  font-family: var(--fontZen);
}

.main .mainContent .description{
  letter-spacing: 1.5px;
  font-size: 1.6rem;

}

.main .mainBTN a{
  margin: 20px;
  font-family: var(--fontZen);
  display: inline-block;
  padding: 15px;
  text-decoration: none;
  font-size: 0.8rem;
  color: var(--dark1);
  background: var(--light2);
  border-radius: 2em;
  font-weight: bold;
}

.main .mainBTN a:hover{
  color: var(--light2);
  background: var(--dark1);
  box-shadow: 3px 3px 10px var(--dark1),-3px -3px 10px var(--dark1),inset 3px 3px 10px var(--dark1),inset -3px -3px 10px var(--dark1);
  border-color: #212529;
}

.portfolioIMG{
  width: min-content;
  height: min-content;
  margin: auto;

}

.portfolioIMG img{
  object-fit:cover;
  margin: auto;
  width:250px;
  height: 250px;
  border-width: 5px;
  border-radius: 50%;
  border-color: var(--dark2);
  border-style: solid;
}


.app{
  animation-name: fadeInAnimation;
  animation-duration: 5s;
}



@keyframes fadeInAnimation { 
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}















h1.ml8 {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-40%);
  font-weight: 900;
  font-size: 6em;
  font-family: var(--fontNewTagomin);
  text-align: center;
  color: var(--light1);
  width: 3em;
  height: 3em;
}

.ml8 .letters-container {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  height:1.85em;
}

.ml8 .letters {
  position: relative;
  z-index: 2;
  display: inline-block;
  line-height: 0.7em;
  font-size: 0.4em;
  right: -0.12em;
  top: -0.2em;
}

.ml8 .bang {
  font-size: 0.6em;
  top: auto;
  left: -0.06em;
}

.ml8 .circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
}

.ml8 .circle-white {
  width: 3em;
  height: 3em;
  border: 2px dashed var(--light1);
  border-radius: 2em;
}

.ml8 .circle-dark {
  width: 2.2em;
  height: 2.2em;
  background-color: var(--dark2);
  border-radius: 3em;
  z-index: 1;
}

.ml8 .circle-dark-dashed {
  border-radius: 2.4em;
  background-color: transparent;
  border: 2px dashed #4f7b86;
  width: 2.3em;
  height: 2.3em;
}

.loading{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  color: var(--light2);
  font-size: 2rem;
}

.loadingAnimation{
  width: 10vw;
  height: 10vw;
  border-width: 10px;
  border-radius: 50%;
  transform-origin: center;
  border-style:groove;
  border-color: var(--dark2);
  animation:spin 3s linear infinite;
}


@keyframes spin { 
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform:rotate(360deg); 
    } 
}
